.app {
  height: 100vh;
}

@media screen and (min-width: 1024px) {
  .max-w-80percent {
    max-width: 80% !important;
  }
}

.max-w-96percent {
  max-width: 96%;
}

.textarea-custom {
  width: 100%;
  height: 320px;
  padding: 12px 20px;
  border: 0px;
  background-color: #f8f8f8;
  font-size: 20px;
  font-family: "Ubuntu", sans-serif;
  resize: none;
}

.mycustom-scroll::-webkit-scrollbar {
  width: 10px;
}

.mycustom-scroll::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.mycustom-scroll::-webkit-scrollbar-thumb {
  background: #888;
}

.mycustom-scroll::-webkit-scrollbar-thumb:hover {
  background: #555;
}
